import axios from "axios"

export const  getUser=(data)=>{
 return axios.get("http://127.0.0.1/list/all",data)
}
export const  addUser=(data)=>{
 return axios.get("http://127.0.0.1/list/add",data)
}
export const  editUser=(data)=>{
 return axios.get("http://127.0.0.1/list/update",data)
}
export const  delUser=(data)=>{
 return axios.get("http://127.0.0.1/list/del",data)
}
export const  findUser=(data)=>{
 return axios.get("http://127.0.0.1/list/find",data)
}