<template>
    <div>
        <div class="up">
            <div class="up-1">
                <div class="main-1">
                    <el-dialog
                        title="提示"
                        :visible.sync="dialogVisible"
                        width="50%"
                    >
                        <!-- 用户表单验证 -->
                        <el-form
                            ref="form"
                            :rules="rules"
                            :inline="true"
                            :model="form"
                            label-width="80px"
                            :before-close="handleClose"
                        >
                            <el-form-item label="序号" prop="id">
                                <el-input
                                    placeholder="请输入序号"
                                    v-model="form.id"
                                ></el-input>
                            </el-form-item>
                            <el-form-item label="姓名" prop="name">
                                <el-input
                                    placeholder="请输入姓名"
                                    v-model="form.name"
                                ></el-input>
                            </el-form-item>
                            <el-form-item label="年龄" prop="age">
                                <el-input
                                    placeholder="请输入年龄"
                                    v-model="form.age"
                                ></el-input>
                            </el-form-item>
                            <el-form-item label="性别" prop="sex">
                                <el-select
                                    v-model="form.sex"
                                    placeholder="请选择性别"
                                >
                                    <el-option
                                        label="男"
                                        :value="1"
                                    ></el-option>
                                    <el-option
                                        label="女"
                                        :value="0"
                                    ></el-option>
                                </el-select>
                            </el-form-item>

                            <el-form-item label="地址" prop="address">
                                <el-input
                                    placeholder="请输入地址"
                                    v-model="form.address"
                                ></el-input>
                            </el-form-item>
                        </el-form>

                        <span slot="footer" class="dialog-footer">
                            <el-button @click="cancelPopup">取 消</el-button>
                            <el-button type="primary" @click="userAdd"
                                >确 定</el-button
                            >
                        </span>
                    </el-dialog>
                </div>
                <div class="header-1">
                    <el-button type="primary" @click="popUp"> +新增 </el-button>

                    <!-- form搜索区 -->
                    <el-form :inline="true">
                        <el-form-item
                            ><el-input
                                placeholder="请输入名称"
                                v-model="userId"
                            ></el-input
                        ></el-form-item>

                        <el-form-item>
                            <el-button type="primary" @click="findUser"
                                >查询</el-button
                            >
                            <el-button type="primary" @click="delUser"
                                >删除</el-button
                            >
                        </el-form-item>
                    </el-form>
                </div>
                <div class="table-1">
                    <el-table :data="tableData" stripe style="width: 100%">
                        <el-table-column prop="id" label="序号" width="100">
                        </el-table-column>
                        <el-table-column prop="name" label="名字" width="120">
                        </el-table-column>
                        <el-table-column prop="age" label="年龄" width="120">
                        </el-table-column>
                        <el-table-column prop="sex" label="性别" width="200">
                            <template slot-scope="scope">
                                <span style="margin-left: 10px">{{
                                    scope.row.sex == 1 ? "男" : "女"
                                }}</span>
                            </template>
                        </el-table-column>

                        <!-- <el-table-column
                            prop="username"
                            label="账号名"
                            width="180"
                        >
                        </el-table-column>
                        <el-table-column
                            prop="password"
                            label="密码"
                            width="180"
                        >
                        </el-table-column> -->
                        <el-table-column
                            prop="address"
                            label="地址"
                            width="170"
                        >
                        </el-table-column>
                        <el-table-column fixed="right" label="操作" width="300">
                            <template slot-scope="scope">
                                <el-button
                                    @click="
                                        handleClick(scope.row, scope.$index)
                                    "
                                    type="text"
                                    size="small"
                                    >修改</el-button
                                >
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </div>
        </div>
        <div class="down"></div>
    </div>
</template>
<style lang="less" scoped>
.up-1 {
    // padding-top: 50px;
    // padding-left: 250px;
    //width: 100%;
    //height: 600px;
    padding: 20px;
    .header-1 {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
</style>
<script>
import { getUser, addUser, editUser, delUser, findUser } from "../server/index";
import axios from "axios";
export default {
    data() {
        return {
            value1: null,
            modelType: 0,
            // value2: null,
            colors: ["#99A9BF", "#F7BA2A", "#FF9900"],
            userForm: {
                name: "",
            },

            tableData: [],
            userId: "",
            // name: "",
            // address: "",
            // tel: "",
            dialogVisible: false,
            form: {
                name: "",
                age: "",
                sex: "",
                address: "",
                id: "",
                username: "",
                password: "",
            },
            rules: {
                id: [
                    {
                        required: true,
                        message: "请输入序号",
                        trigger: "blur",
                    },
                ],
                name: [
                    {
                        required: true,
                        message: "请输入姓名",
                        trigger: "blur",
                    },
                ],
                age: [
                    {
                        required: true,
                        message: "请输入年龄",
                        trigger: "blur",
                    },
                ],
                sex: [
                    {
                        required: true,
                        message: "请选择性别",
                        trigger: "blur",
                    },
                ],

                address: [
                    {
                        required: true,
                        message: "请输入联系电话",
                        trigger: "blur",
                    },
                ],
            },
        };
    },
    computed: {},
    methods: {
        //用户删除
        delUser() {
            //console.log(this.userId);

            delUser({ params: { id: this.userId } }).then((res) => {
                console.log(res.data);
                if (res.data.status == 200) {
                    this.getUserList();
                } else {
                    this.$message({
                        message: "删除失败",
                        type: "error",
                    });
                }
            });
            // axios
            //     .get("http://127.0.0.1/list/del", {
            //         params: {
            //             id: this.userId,
            //         },
            //     })
            //     .then((res) => {
            //         console.log(res.data);
            //         if (res.data.status == 200) {
            //             this.getUserList();
            //         } else {
            //             this.$message({
            //                 message: "删除失败",
            //                 type: "error",
            //             });
            //         }
            //     })
            //     .catch((err) => {
            //         console.log("操作失败" + err);
            //     });
        },
        //获取列表
        getUserList() {
            getUser().then((res) => {
                this.tableData = res.data;
            });
            // axios
            //     .get("http://127.0.0.1/list/all")
            //     .then((res) => {
            //         console.log(res.data);
            //         this.tableData = res.data;
            //     })
            //     .catch((err) => {
            //         console.log("获取数据失败" + err);
            //     });
        },
        //查找功能
        findUser() {
            if (this.userId == "") {
                this.getUserList();
            } else {
                findUser({ params: { id: this.userId } }).then((res) => {
                    this.tableData = res.data;
                });
                // axios
                //     .get("http://127.0.0.1/list/find", {
                //         params: {
                //             id: this.userId,
                //         },
                //     })
                //     .then((res) => {
                //         // console.log(res.data);
                //         this.tableData = res.data;
                //         //this.getUserList();
                //     })
                //     .catch((err) => {
                //         console.log("操作失败" + err);
                //     });
            }
        },
        //取消
        cancelPopup() {
            this.$refs.form.resetFields();
        },
        handleClose() {
            this.dialogVisible = false;
        },
        //新增用户
        popUp() {
            this.modelType = 0;
            this.dialogVisible = true;
        },
        cancelPopup() {
            this.dialogVisible = false;
        },
        //信息框确定按钮
        userAdd() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    if (this.modelType == 0) {
                        //新增用户
                        addUser({
                            params: {
                                id: this.form.id,
                                name: this.form.name,
                                address: this.form.address,
                                age: this.form.age,
                                sex: this.form.sex,
                            },
                        }).then((res) => {
                            // console.log(res.data);
                            if (res.data.status == 200) {
                                this.getUserList();
                            } else {
                                this.$message({
                                    message: "添加失败",
                                    type: "error",
                                });
                            }
                        });
                    } else {
                        editUser({
                            params: {
                                id: this.form.id,
                                name: this.form.name,
                                address: this.form.address,
                                age: this.form.age,
                                sex: this.form.sex,
                            },
                        }).then((res) => {
                            if (res.data.status == 200) {
                                this.getUserList();
                                this.$message({
                                    message: "修改成功",
                                    type: "success",
                                });
                            } else {
                                console.log(res);
                                this.$message({
                                    message: "修改失败",
                                    type: "error",
                                });
                            }
                        });

                        this.$refs.form.resetFields();
                        //关闭弹窗
                        this.dialogVisible = false;
                    }
                }
            });

            this.$refs.form.resetFields();
            this.dialogVisible = false;
        },

        //用户修改功能
        handleClick(row, i) {
            this.modelType = 1;
            this.dialogVisible = true;
            this.form = row;
            this.index = i;
        },
    },
    mounted() {
        this.getUserList();
    },
};
</script>
